import React from "react"
import {
    Button
} from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"
import Can from '../../../../../utility/permissions/can';

class FeeCategoryTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Fee Category Code",
                selector: "FEECATEGORY_CODE",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEECATEGORY_CODE}</p>
            }
            ,
            {
                name: "Fee Category Name",
                selector: "FEECATEGORY_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEECATEGORY_NAME}</p>
            }
            ,
            {
                name: "Fee Category Description",
                selector: "FEECATEGORY_DESCRIPTION",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEECATEGORY_DESCRIPTION}</p>
            }
            ,
            // {
            //     name: "Relative Size",
            //     selector: "RELATIVE_SIZE",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.RELATIVE_SIZE}</p>
            // },
            {
                name: "Department Name",
                selector: "DEPARTMENT_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SET_DEPARTMENTS.DEPARTMENT_NAME}</p>
            }
            ,
            {
                name: "Module Name",
                selector: "MODULE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.Set_Modules.MODULE_NAME}</p>
            },
            {
                name: "COA Decription",
                selector: "COA_DESCRIPTION",
                sortable: true,
                cell: row => <p className="mb-0">{row.SET_CHART_OF_ACCOUNTS.COA_DESCRIPTION}</p>
            },
            // {
            //     name: "COA Category Code",
            //     selector: "COACATEGORY_CODE",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.COACategory.COACATEGORY_CODE}</p>
            // },
            // {
            //     name: "COA Sub Category Code",
            //     selector: "COASUBCATEGORY_CODE",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.COASubCategory.COASUBCATEGORY_CODE}</p>
            // }
            // ,
            {
                name: "Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYSTEMSTATUS.SYS_STATUS_NAME}</p>
            }
        ]
    }

    render() {

        return (
            <>
            <DataTable
                className="dataTable-custom"
                data={this.props.value.length ? this.props.filteredData : this.props.data}
                columns={this.tableProperties.columns}
                noHeader
                pagination
                subHeader
                striped
                responsive
                subHeaderComponent={
                    <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                }
            />
            </>

        )
    }
}


export default FeeCategoryTable