import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import FeeCategoryTable from './Tables/FeeCategoryTable';
import FeeCategoryModal from './Modals/FeeCategoryModal';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import ModuleSearch from '../../SearchComponents/ModuleSearch/ModuleSearch';
import DepartmentSearch from '../../SearchComponents/DepartmentSearch/DepartmentSearch';
import COASearch from '../../SearchComponents/COASearch/COASearch';
import COACategorySearch from '../../SearchComponents/COACategorySearch/COACategorySearch';
import COASubCategorySearch from '../../SearchComponents/COASubCategorySearch/COASubCategorySearch';

import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class FeeCategory extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            filteredmoduleData: [],
            filtereddepartmentData: [],
            filteredCOAData: [],
            filteredCOACategoryData: [],
            filteredCOASubCategoryData: [],

            value: '',
            show: false,
            showdelete: false,
            FEECATEGORY_ID: 0,
            FEECATEGORY_CODE: '',
            FEECATEGORY_NAME: '',
            FEECATEGORY_DESCRIPTION: '',
            RELATIVE_SIZE: '',
            COA_ID: 0,
            COACATEGORY_ID: 0,
            COASUBCATEGORY_ID: 0,
            DEPARTMENT_ID: 0,
            MODULE_ID: 0,
            SYS_STATUS_ID: 0,
            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            openmodulesearch: false,
            opendepartmentsearch: false,
            openCOAsearch: false,
            openCOACategorysearch: false,
            openCOASubCategorysearch: false,

            SYSTEMSTATUSTYPEDESCRIPTION: '',
            COA_DESCRIPTION: '',
            COASUBCATEGORY_CODE: '',
            COACATEGORY_CODE: '',
            DEPARTMENT_NAME: '',
            MODULE_NAME: '',
            searchcolumns: [],
            searchdata: [],
            searchdepartmentdata: [],
            searchCOAdata: [],
            searchCOACategorydata: [],
            searchCOASubCategorydata: [],

            searchmoduledata: [],
            searchFilteredData: [],
            searchvalue: '',
            searchmodulevalue: '',
            searchdepartmentvalue: '',
            searchCOAvalue: '',
            searchCOACategoryvalue: '',
            searchCOASubCategoryvalue: '',
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };
        this.handleModuleSearch = () => {
            this.setState({ openmodulesearch: true });
        };
        this.handleDepartmentSearch = () => {
            this.setState({ opendepartmentsearch: true });
        };
        this.handleCOASearch = () => {
            this.setState({ openCOAsearch: true });
        };
        this.handleCOACategorySearch = () => {
            this.setState({ openCOACategorysearch: true });
        };
        this.handleCOASubCategorySearch = () => {
            this.setState({ openCOASubCategorysearch: true });
        };
        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.handleHideModuleSearch = () => {
            this.setState({ openmodulesearch: false });
        };
        this.handleHideDepartmentSearch = () => {
            this.setState({ opendepartmentsearch: false });
        };

        this.handleHideCOASearch = () => {
            this.setState({ openCOAsearch: false });
        };

        this.handleHideCOACategorySearch = () => {
            this.setState({ openCOACategorysearch: false });
        };
        this.handleHideCOASubCategorySearch = () => {
            this.setState({ openCOASubCategorysearch: false });
        };
        this.populateData();
        this.populateTypes();
        this.populateDepartments();
        this.populateCOA();
    }

    populateData() {
        const getdata = `
        query {
            FeeCategories  {
            FEECATEGORY_ID,
            FEECATEGORY_CODE,
            FEECATEGORY_NAME,
            FEECATEGORY_DESCRIPTION,
            RELATIVE_SIZE,
            SET_COA_ID,
            SET_CHART_OF_ACCOUNTS{
                      COA_ID,
                      COA_DESCRIPTION
                            },
            COACATEGORY_ID: SET_COACATEGORY_ID
            COACategory{
                        COACATEGORY_ID
                        COACATEGORY_CODE
                        }
            COASUBCATEGORY_ID: SET_COA_SUBCATEGORY_ID
            COASubCategory{
                        COASUBCATEGORY_ID
                        COASUBCATEGORY_CODE
                          }
            SET_DEPARTMENT_ID
            SET_DEPARTMENTS{
                    DEPARTMENT_ID,
                    DEPARTMENT_NAME
                           },
            SYS_STATUS_ID,
            SYSTEMSTATUS{
                    SYS_STATUS_ID,
                    SYS_STATUS_NAME
                      },
            MODULE_ID: SET_MODULE_ID,
            Set_Modules{
                    MODULE_ID,
                    MODULE_NAME
                    },
            CREATEDBY,
            MODIFIEDBY,
            CREATED_DATE,
            MODIFIED_DATE
            }
        }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.FeeCategories
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })
    }

    populateTypes() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    populateModules(value) {
        const getdata = `
            query {
                ModuleFilter(
                    SET_DEPARTMENT_ID:${value}
                    )
                {
                        MODULE_ID,
                        MODULE_NAME
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchmoduledata: data.data.ModuleFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Module Load Data');
                }
            })
    }
    populateDepartments() {
        const getdata = `
            query {
                Departments{
                        DEPARTMENT_ID,
                        DEPARTMENT_NAME
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdepartmentdata: data.data.Departments
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Department Load Data');
                }
            })
    }

    populateCOA() {
        const getdata = `
            query {
                ChartOfAccounts  {
                    COA_ID,
                  COA_DESCRIPTION,
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchCOAdata: data.data.ChartOfAccounts
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Chart of Account Load Data');
                }
            })
    }
    populateCOACategorys(value) {
        const getdata = `
            query {
                COACategorysFilter(
                    SET_COA_ID:${value}
                    )
                {
                        COACATEGORY_ID,
                        COACATEGORY_CODE
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchCOACategorydata: data.data.COACategorysFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to COA Category Load Data');
                }
            })
    }

    populateCOASubCategorys(value) {
        const getdata = `
            query {
                COASubCategorysFilter(
                    SET_COA_CATEGORY_ID:${value}
                    )
                {
                    COASUBCATEGORY_ID
                    COASUBCATEGORY_CODE
                }
            }
        `;
        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchCOASubCategorydata: data.data.COASubCategorysFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to COA Subcategory Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            const form = `
        mutation{
            addFeeCategory(
                FEECATEGORY_CODE:"${this.state.FEECATEGORY_CODE}",
                FEECATEGORY_NAME:"${this.state.FEECATEGORY_NAME}",
                FEECATEGORY_DESCRIPTION:"${this.state.FEECATEGORY_DESCRIPTION}",
                RELATIVE_SIZE:"${this.state.RELATIVE_SIZE}",
                SET_COA_ID:${this.state.COA_ID},
                SET_COACATEGORY_ID:${this.state.COACATEGORY_ID},
                SET_COA_SUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_MODULE_ID:${this.state.MODULE_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    FEECATEGORY_ID
            }
        }
        `;
            
            const formEdit = `
        mutation{
            updateFeeCategory(
                FEECATEGORY_ID:${this.state.FEECATEGORY_ID},
                FEECATEGORY_CODE:"${this.state.FEECATEGORY_CODE}",
                FEECATEGORY_NAME:"${this.state.FEECATEGORY_NAME}",
                FEECATEGORY_DESCRIPTION:"${this.state.FEECATEGORY_DESCRIPTION}",
                RELATIVE_SIZE:"${this.state.RELATIVE_SIZE}",
                SET_COA_ID:${this.state.COA_ID},
                SET_COACATEGORY_ID:${this.state.COACATEGORY_ID},
                SET_COA_SUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_MODULE_ID:${this.state.MODULE_ID},
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    FEECATEGORY_ID
            }
        }
        `;
            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();
        const formDelete = `
        mutation{
            deleteFeeCategory(
                FEECATEGORY_ID:${this.state.FEECATEGORY_ID}
                ){
                    FEECATEGORY_ID
            }
        }
        `;
        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {
        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });
    };


    handleCloseModuleSearch = (e) => {
        this.setState({
            MODULE_ID: e.MODULE_ID,
            MODULE_NAME: e.MODULE_NAME,
            openmodulesearch: false,
        });
    };

    handleCloseDepartmentSearch = (e) => {
        this.setState({
            DEPARTMENT_ID: e.DEPARTMENT_ID,
            DEPARTMENT_NAME: e.DEPARTMENT_NAME,
            opendepartmentsearch: false,
        });
        this.populateModules(e.DEPARTMENT_ID);
    };


    handleCloseCOACategorySearch = (e) => {
        this.setState({
            COACATEGORY_ID: e.COACATEGORY_ID,
            COACATEGORY_CODE: e.COACATEGORY_CODE,
            openCOACategorysearch: false,
        });
        this.populateCOASubCategorys(e.COACATEGORY_ID);
    };
    handleCloseCOASubCategorySearch = (e) => {
        this.setState({
            COASUBCATEGORY_ID: e.COASUBCATEGORY_ID,
            COASUBCATEGORY_CODE: e.COASUBCATEGORY_CODE,
            openCOASubCategorysearch: false,
        });
    };

    handleCloseCOASearch = (e) => {
        this.setState({
            COA_ID: e.COA_ID,
            COA_DESCRIPTION: e.COA_DESCRIPTION,
            openCOAsearch: false,
        });
        this.populateCOACategorys(e.COA_ID);
    };


    handleEditData = (e) => {
        this.setState({
            FEECATEGORY_ID: e.FEECATEGORY_ID,
            FEECATEGORY_CODE: e.FEECATEGORY_CODE,
            FEECATEGORY_NAME: e.FEECATEGORY_NAME,
            FEECATEGORY_DESCRIPTION: e.FEECATEGORY_DESCRIPTION,
            RELATIVE_SIZE: e.RELATIVE_SIZE,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            COA_ID: e.SET_COA_ID,
            COACATEGORY_ID: e.COACATEGORY_ID,
            COASUBCATEGORY_ID: e.COASUBCATEGORY_ID,
            DEPARTMENT_ID: e.SET_DEPARTMENT_ID,
            MODULE_ID: e.MODULE_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            MODULE_NAME: e.Set_Modules.MODULE_NAME,
            DEPARTMENT_NAME: e.SET_DEPARTMENTS.DEPARTMENT_NAME,
            COA_DESCRIPTION: e.SET_CHART_OF_ACCOUNTS.COA_DESCRIPTION,
            COACATEGORY_CODE: e.COACategory.COACATEGORY_CODE,
            COASUBCATEGORY_CODE: e.COASubCategory.COASUBCATEGORY_CODE,
            show: true,
            InsertUpdate: 'Update'
        })
        this.populateModules(e.SET_DEPARTMENT_ID);
        this.populateCOACategorys(e.SET_COA_ID);
        this.populateCOASubCategorys(e.COACATEGORY_ID);
    }

    handleDeleteData = (e) => {
        this.setState({
            FEECATEGORY_ID: e.FEECATEGORY_ID,
            FEECATEGORY_CODE: e.FEECATEGORY_CODE,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            FEECATEGORY_CODE: '',
            FEECATEGORY_NAME: '',
            FEECATEGORY_DESCRIPTION: '',
            RELATIVE_SIZE: '',
            SYS_STATUS_ID: '',
            SET_MODULE_ID: '',
            SET_COA_ID: '',
            SET_COACATEGORY_ID: '',
            SET_COA_SUBCATEGORY_ID: '',
            SET_DEPARTMENT_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            DEPARTMENT_NAME: '',
            COA_DESCRIPTION: '',
            MODULE_NAME: '',
            COACATEGORY_CODE: '',
            COASUBCATEGORY_CODE: '',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.FEECATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.FEECATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchModuleFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchmoduledata
        let filteredmoduleData = this.state.searchFilteredData
        this.setState({ searchmodulevalue: value });

        if (value.length) {
            filteredmoduleData = data.filter(item => {
                let startsWithCondition =
                    item.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.MODULE_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredmoduleData })
        }
    }

    handleSearchDepartmentFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdepartmentdata
        let filtereddepartmentData = this.state.searchFilteredData
        this.setState({ searchdepartmentvalue: value });

        if (value.length) {
            filtereddepartmentData = data.filter(item => {
                let startsWithCondition =
                    item.DEPARTMENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.DEPARTMENT_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filtereddepartmentData })
        }
    }
    handleSearchCOAFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchCOAdata
        let filteredCOAData = this.state.searchFilteredData
        this.setState({ searchCOAvalue: value });

        if (value.length) {
            filteredCOAData = data.filter(item => {
                let startsWithCondition =
                    item.COA_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.COA_DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredCOAData })
        }
    }

    handleSearchCOACategoryFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchCOACategorydata
        let filteredCOACategoryData = this.state.searchFilteredData
        this.setState({ searchCOACategoryvalue: value });
        if (value.length) {
            filteredCOACategoryData = data.filter(item => {
                let startsWithCondition =
                    item.COACATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.COACATEGORY_CODE.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredCOACategoryData })
        }
    }

    handleSearchCOASubCategoryFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchCOASubCategorydata
        let filteredCOASubCategoryData = this.state.searchFilteredData
        this.setState({ searchCOASubCategoryvalue: value });

        if (value.length) {
            filteredCOASubCategoryData = data.filter(item => {
                let startsWithCondition =
                    item.COASUBCATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.COASUBCATEGORY_CODE.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredCOASubCategoryData })
        }
    }
    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });
        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, value,
            filteredData, filteredmoduleData, filtereddepartmentData, filteredCOAData, filteredCOACategoryData, filteredCOASubCategoryData,
            searchdata, searchmoduledata, searchdepartmentdata, searchCOAdata, searchCOACategorydata, searchCOASubCategorydata,
            searchvalue, searchmodulevalue, searchdepartmentvalue, searchCOAvalue, searchCOACategoryvalue, searchCOASubCategoryvalue } = this.state
        return (
            <>
                <Helmet><title>Fee Categories</title></Helmet>
                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Fee Categories"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Fee Categories</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">
                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />
                                    <DepartmentSearch
                                        handleSearchDepartment={this.handleCloseDepartmentSearch}
                                        opensearch={this.state.opendepartmentsearch}
                                        handleHideSearch={this.handleHideDepartmentSearch}
                                        value={searchdepartmentvalue}
                                        data={searchdepartmentdata}
                                        handleFilter={this.handleSearchDepartmentFilter}
                                        filteredData={filtereddepartmentData}
                                    />
                                    <COASearch
                                        handleSearchCOA={this.handleCloseCOASearch}
                                        opensearch={this.state.openCOAsearch}
                                        handleHideSearch={this.handleHideCOASearch}
                                        value={searchCOAvalue}
                                        data={searchCOAdata}
                                        handleFilter={this.handleSearchCOAFilter}
                                        filteredData={filteredCOAData}
                                    />
                                    <COACategorySearch
                                        handleSearchCOACategory={this.handleCloseCOACategorySearch}
                                        opensearch={this.state.openCOACategorysearch}
                                        handleHideSearch={this.handleHideCOACategorySearch}
                                        value={searchCOACategoryvalue}
                                        data={searchCOACategorydata}
                                        handleFilter={this.handleSearchCOACategoryFilter}
                                        filteredData={filteredCOACategoryData}
                                    />
                                    <COASubCategorySearch
                                        handleSearchCOASubCategory={this.handleCloseCOASubCategorySearch}
                                        opensearch={this.state.openCOASubCategorysearch}
                                        handleHideSearch={this.handleHideCOASubCategorySearch}
                                        value={searchCOASubCategoryvalue}
                                        data={searchCOASubCategorydata}
                                        handleFilter={this.handleSearchCOASubCategoryFilter}
                                        filteredData={filteredCOASubCategoryData}
                                    />
                                    <ModuleSearch
                                        handleSearchModule={this.handleCloseModuleSearch}
                                        opensearch={this.state.openmodulesearch}
                                        handleHideSearch={this.handleHideModuleSearch}
                                        value={searchmodulevalue}
                                        data={searchmoduledata}
                                        handleFilter={this.handleSearchModuleFilter}
                                        filteredData={filteredmoduleData}
                                    />
                                    <FeeCategoryTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />
                                    <FeeCategoryModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}
                                        handleModuleSearch={this.handleModuleSearch}
                                        handleDepartmentSearch={this.handleDepartmentSearch}
                                        handleCOASearch={this.handleCOASearch}
                                        handleCOACategorySearch={this.handleCOACategorySearch}
                                        handleCOASubCategorySearch={this.handleCOASubCategorySearch}

                                        DEPARTMENT_NAME={this.state.DEPARTMENT_NAME}
                                        COA_DESCRIPTION={this.state.COA_DESCRIPTION}
                                        COASUBCATEGORY_CODE={this.state.COASUBCATEGORY_CODE}
                                        COACATEGORY_CODE={this.state.COACATEGORY_CODE}
                                        MODULE_NAME={this.state.MODULE_NAME}
                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        FEECATEGORY_CODE={this.state.FEECATEGORY_CODE}
                                        FEECATEGORY_NAME={this.state.FEECATEGORY_NAME}
                                        FEECATEGORY_DESCRIPTION={this.state.FEECATEGORY_DESCRIPTION}
                                        RELATIVE_SIZE={this.state.RELATIVE_SIZE}

                                        changeFeeCategoryCode={e => this.setState({ FEECATEGORY_CODE: e.target.value })}
                                        changeFeeCategoryName={e => this.setState({ FEECATEGORY_NAME: e.target.value })}
                                        changeFeeCategoryDescription={e => this.setState({ FEECATEGORY_DESCRIPTION: e.target.value })}
                                        changeRelativeSize={e => this.setState({ RELATIVE_SIZE: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}
                                    />
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>
            </>
        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(FeeCategory));